3Z\" fill=\"#{$color}\"/></svg>');\n}\n","/* ------------------------------------ *\\\n    $RESET\n\\* ------------------------------------ */\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  font-family: $ff-font;\n  font-size: $font-size-body;\n  background: $c-gray--lightest;\n  color: $c-body-color;\n}\n\nblockquote,\nbody,\ndiv,\nfigure,\nfooter,\nform,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nheader,\nhtml,\niframe,\nlabel,\nlegend,\nli,\nnav,\nobject,\nol,\np,\nsection,\ntable,\nul {\n  margin: 0;\n  padding: 0;\n}\n\narticle,\nfigure,\nfooter,\nheader,\nhgroup,\nnav,\nsection {\n  display: block;\n}\n\naddress {\n  font-style: normal;\n}\n\nimg,\nvideo,\nobject,\nsvg,\niframe {\n  max-width: 100%;\n  border: none;\n  display: block;\n}\n\na {\n  text-decoration: none;\n  transition: $transition-all;\n  color: inherit;\n\n  svg path {\n    fill: currentColor;\n  }\n}\n","/* ------------------------------------ *\\\n    $HEADINGS\n\\* ------------------------------------ */\n\n@mixin o-heading--xxl {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xxl;\n  font-style: normal;\n  font-weight: 800;\n  text-transform: normal;\n  line-height: 1.2;\n  letter-spacing: normal;\n}\n\nh1,\n.o-heading--xxl {\n  @include o-heading--xxl;\n}\n\n@mixin o-heading--xl {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xl;\n  font-style: normal;\n  font-weight: 800;\n  text-transform: normal;\n  line-height: 1.2;\n  letter-spacing: normal;\n}\n\nh2,\n.o-heading--xl {\n  @include o-heading--xl;\n}\n\n@mixin o-heading--l {\n  font-family: $ff-font--primary;\n  font-size: $font-size-l;\n  font-style: normal;\n  font-weight: 600;\n  text-transform: inherit;\n  line-height: 1.4;\n  letter-spacing: normal;\n}\n\nh3,\n.o-heading--l {\n  @include o-heading--l;\n}\n\n@mixin o-heading--m {\n  font-family: $ff-font--primary;\n  font-size: $font-size-m;\n  font-style: normal;\n  font-weight: 600;\n  line-height: 1.6;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n}\n\nh4,\n.o-heading--m {\n  @include o-heading--m;\n}\n\n@mixin o-heading--s {\n  font-family: $ff-font--primary;\n  font-size: $font-size-s;\n  font-style: normal;\n  font-weight: 500;\n  line-height: 1.6;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n}\n\nh5,\n.o-heading--s {\n  @include o-heading--s;\n}\n\n@mixin o-heading--xs {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xs;\n  font-style: normal;\n  font-weight: bold;\n  line-height: 1.6;\n  text-transform: uppercase;\n  letter-spacing: 1.2px;\n}\n\nh6,\n.o-heading--xs {\n  @include o-heading--xs;\n}\n","/* ------------------------------------*\\\n    $SPACING\n\\*------------------------------------ */\n\n$sizes: (\"\": $space, --quarter: $space / 4, --half: $space / 2, --and-half: $space * 1.5, --double: $space * 2, --triple: $space * 3, --quad: $space * 4, --zero: 0);\n\n$sides: (\"\": \"\", --top: \"-top\", --bottom: \"-bottom\", --left: \"-left\", --right: \"-right\");\n\n@each $size_key, $size_value in $sizes {\n  .u-spacing#{$size_key} {\n    & > * + * {\n      margin-top: #{$size_value};\n    }\n  }\n\n  @each $side_key, $side_value in $sides {\n    .u-padding#{$size_key}#{$side_key} {\n      padding#{$side_value}: #{$size_value};\n    }\n\n    .u-space#{$size_key}#{$side_key} {\n      margin#{$side_value}: #{$size_value};\n    }\n  }\n}\n\n.u-spacing--left {\n  & > * + * {\n    margin-left: $space;\n  }\n}\n","/* ------------------------------------ *\\\n    $LAYOUT\n\\* ------------------------------------ */\n\nbody {\n  position: relative;\n\n  &::before {\n    content: \"\";\n    display: block;\n    height: 100vh;\n    width: 100vw;\n    background-color: $c-overlay;\n    position: fixed;\n    top: 0;\n    left: 0;\n    transition: all 0.5s ease;\n    transition-delay: 0.25s;\n    opacity: 0;\n    visibility: hidden;\n    z-index: 0;\n  }\n}\n\n/**\n * Wrapping element to keep content contained and centered.\n */\n@mixin l-w